import React, { useState } from "react";
import { Grid, Input, Button, Toast, NavBar } from "antd-mobile";

const SmsVerification: React.FC = () => {
  const [values, setValues] = useState<string[]>(Array(6).fill(""));

  // 
  const handleInputChange = (value: string, index: number) => {
    const newValues = [...values];
    newValues[index] = value.slice(0, 1); // 
    setValues(newValues);

    // 
    if (value && index < 5) {
      const nextInput = document.getElementById(`input-${index + 1}`);
      nextInput?.focus();
    }
  };

  const handleSubmit = () => {
    const code = values.join("");
    if (code.length === 6) {
      Toast.show({
        content: `${code}`,
        duration: 2000,
      });
    } else {
      Toast.show({
        content: "",
        duration: 2000,
      });
    }
  };

  return (
    <div style={styles.container}>
      <NavBar style={{ marginTop: '20px' }} />
      <div style={styles.wrap}>
          {/*  */}
      <h3 style={styles.title}></h3>
        <p style={styles.subtitle}>
          <span style={styles.phone}>+86 13********1</span>
           </p>
        
  {/*  */}
      <Grid columns={6} gap={8}>
        {values.map((value, index) => (
          <Input
            key={index}
            id={`input-${index}`}
            value={value}
            onChange={(e) => handleInputChange(e, index)}
            maxLength={1}
            style={styles.inputBox}
            type="number"
          />
        ))}
      </Grid>
 {/*  */}
      <p style={styles.resendText}>
        <span style={{ color: "#888" }}> (58)</span>
        </p>
       


      </div>
    

    

     

      

      {/*  */}
      <p style={styles.footer}>
        
        <a href="#" style={styles.link}>
          
        </a>
      </p>
    </div>
  );
};

export default SmsVerification;

